.datingCards__container {
  display: flex;
  justify-content: center;
  margin-top: 5vh;
}
.card {
  position: relative;
  background-color: white;
  width: 400px;
  padding: 20px;
  max-width: 50vw;
  height: 50vh;
  box-shadow: 0px 10px 40px 0px rgba(0, 0, 0, 0.15);
  border-radius: 20px;
  background-size: cover;
  background-position: center;
}
.swipe{
    position: absolute;
}
.cardContent{
    width: 100%;
    height: 100%;
}
.card h3{
    position: absolute;
    bottom: 0;
    margin: 10px;
    color: white;
}
